.choice {
  text-align: center;
  margin-top: 40px;
}

.information {
  background-color: #32383e;
  border: 1px solid rgba(0, 0, 0, 0.6);
  border-radius: 5px;
  padding: 10px;
}

.information p,
.information h2 {
  text-align: center;
}

.information img {
  display: block;
  margin: 0 auto;
}

.information span {
  font-weight: bold;
}
