.menu {
  display: -webkit-flex;
  display: flex;
  max-width: 600px;
  width: 100%;
  margin: 0 auto;
  margin-top: 40px;
  padding: 20px;
}

.menu li {
  width: calc(100% / 3);
  text-align: center;
}

.wrapper-menu {
  border: 1px solid transparent;
}

.app-title a {
  font-family: "Star Jedi Hollow";
  text-align: center;
  display: block;
  font-size: 45px;
  color: yellow;
  text-decoration: none;
}

.app-title a:visited {
  color: yellow;
}

.menu a {
  text-decoration: none;
  font-size: 28px;
  display: block;
  text-transform: uppercase;
  transition: all 0.3s ease-in-out;
}

.menu a:hover {
  color: darkgrey;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

@media (max-width: 600px) {
  .menu li {
    width: 80%;
    margin-bottom: 15px;
  }
  .menu {
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
  }
}
*,
*:before,
*:after {
  box-sizing: border-box;
  outline: none;
}

.loader {
  position: relative;
  display: grid;
  grid-template-columns: 33% 33% 33%;
  grid-gap: 2px;
  width: 100px;
  height: 100px;
}

.loader > div {
  position: relative;
  display: inline-block;
  width: 100%;
  height: 100%;
  background: yellow;
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transform-origin: center center;
          transform-origin: center center;
  -webkit-animation: loader 2s infinite linear;
          animation: loader 2s infinite linear;
}

div:nth-of-type(1),
div:nth-of-type(5),
div:nth-of-type(9) {
  -webkit-animation-delay: 0.4s;
          animation-delay: 0.4s;
}

div:nth-of-type(4),
div:nth-of-type(8) {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}

div:nth-of-type(2),
div:nth-of-type(6) {
  -webkit-animation-delay: 0.6s;
          animation-delay: 0.6s;
}

div:nth-of-type(3) {
  -webkit-animation-delay: 0.8s;
          animation-delay: 0.8s;
}

@-webkit-keyframes loader {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  80% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
}

@keyframes loader {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  80% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
}

.planet {
  background-color: #32383e;
  border: 1px solid rgba(0, 0, 0, 0.6);
  border-radius: 5px;
  width: 50%;
  margin: 0 auto;
  padding: 10px;
  margin-top: 50px;
}

.planet p,
.planet h2 {
  text-align: center;
}

.planet img {
  display: block;
  margin: 0 auto;
}

.planet span {
  font-weight: bold;
}

@media(max-width: 600px) {
  .planet {
    width: 80%;
  }
}
.item-list li {
  padding: 10px 15px;
  background-color: #32383e;
  border: 1px solid rgba(0, 0, 0, 0.6);
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.item-list li:hover {
  color: darkgrey;
}
.choice {
  text-align: center;
  margin-top: 40px;
}

.information {
  background-color: #32383e;
  border: 1px solid rgba(0, 0, 0, 0.6);
  border-radius: 5px;
  padding: 10px;
}

.information p,
.information h2 {
  text-align: center;
}

.information img {
  display: block;
  margin: 0 auto;
}

.information span {
  font-weight: bold;
}

body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  color: white;
}

@font-face {
  font-family: "Star Jedi Hollow";
  src: url(/star-wars-db/static/media/Starjhol.85f6c668.eot) format("eot"), url(/star-wars-db/static/media/Starjhol.d8ab6dea.woff) format("woff"),
    url(/star-wars-db/static/media/Starjhol.53c0bf82.ttf) format("truetype"),
    url(/star-wars-db/static/media/Starjhol.72efad9d.svg#StarJediHollow) format("svg");
  font-weight: normal;
  font-style: normal;
}

img {
  max-width: 100%;
  height: auto;
}

.star {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: -97;
  display: block;
  background: #000
    url("http://www.script-tutorials.com/demos/360/images/stars.png");
  background-size: cover;
}

.container {
  max-width: 980px;
  width: 100%;
  margin: 0 auto;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a:visited {
  color: white;
}

a {
  color: white;
}

.blink {
  z-index: -97;
  position: fixed;
  width: 100%;
  height: 100%;
  display: block;
  background: url("http://www.script-tutorials.com/demos/360/images/twinkling.png");
  background-size: 10%;
  -webkit-animation: backgroundBlink 30s linear infinite;
          animation: backgroundBlink 30s linear infinite;
}

.wrapper-description {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.wrapper-description div {
  width: calc((100% / 2) - 30px);
}

.page-title {
  text-align: center;
  font-family: "Star Jedi Hollow";
  margin-top: 40px;
  font-size: 30px;
  margin-bottom: 40px;
}

@media (max-width: 600px) {
  .wrapper-description div {
    width: 100%;
  }
  .wrapper-description {
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
  }
  .information {
    margin-top: 30px;
  }
}

@-webkit-keyframes backgroundBlink {
  from {
    background-position: 0% 0%;
  }
  to {
    background-position: 100% 100%;
  }
}

@keyframes backgroundBlink {
  from {
    background-position: 0% 0%;
  }
  to {
    background-position: 100% 100%;
  }
}

