body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  color: white;
}

@font-face {
  font-family: "Star Jedi Hollow";
  src: url("Starjhol.eot?") format("eot"), url("Starjhol.woff") format("woff"),
    url("Starjhol.ttf") format("truetype"),
    url("Starjhol.svg#StarJediHollow") format("svg");
  font-weight: normal;
  font-style: normal;
}

img {
  max-width: 100%;
  height: auto;
}

.star {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: -97;
  display: block;
  background: #000
    url("http://www.script-tutorials.com/demos/360/images/stars.png");
  background-size: cover;
}

.container {
  max-width: 980px;
  width: 100%;
  margin: 0 auto;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a:visited {
  color: white;
}

a {
  color: white;
}

.blink {
  z-index: -97;
  position: fixed;
  width: 100%;
  height: 100%;
  display: block;
  background: url("http://www.script-tutorials.com/demos/360/images/twinkling.png");
  background-size: 10%;
  animation: backgroundBlink 30s linear infinite;
}

.wrapper-description {
  display: flex;
  justify-content: space-between;
}

.wrapper-description div {
  width: calc((100% / 2) - 30px);
}

.page-title {
  text-align: center;
  font-family: "Star Jedi Hollow";
  margin-top: 40px;
  font-size: 30px;
  margin-bottom: 40px;
}

@media (max-width: 600px) {
  .wrapper-description div {
    width: 100%;
  }
  .wrapper-description {
    flex-wrap: wrap;
  }
  .information {
    margin-top: 30px;
  }
}

@keyframes backgroundBlink {
  from {
    background-position: 0% 0%;
  }
  to {
    background-position: 100% 100%;
  }
}
