.menu {
  display: flex;
  max-width: 600px;
  width: 100%;
  margin: 0 auto;
  margin-top: 40px;
  padding: 20px;
}

.menu li {
  width: calc(100% / 3);
  text-align: center;
}

.wrapper-menu {
  border: 1px solid transparent;
}

.app-title a {
  font-family: "Star Jedi Hollow";
  text-align: center;
  display: block;
  font-size: 45px;
  color: yellow;
  text-decoration: none;
}

.app-title a:visited {
  color: yellow;
}

.menu a {
  text-decoration: none;
  font-size: 28px;
  display: block;
  text-transform: uppercase;
  transition: all 0.3s ease-in-out;
}

.menu a:hover {
  color: darkgrey;
  transform: scale(1.2);
}

@media (max-width: 600px) {
  .menu li {
    width: 80%;
    margin-bottom: 15px;
  }
  .menu {
    flex-wrap: wrap;
    justify-content: center;
  }
}