.planet {
  background-color: #32383e;
  border: 1px solid rgba(0, 0, 0, 0.6);
  border-radius: 5px;
  width: 50%;
  margin: 0 auto;
  padding: 10px;
  margin-top: 50px;
}

.planet p,
.planet h2 {
  text-align: center;
}

.planet img {
  display: block;
  margin: 0 auto;
}

.planet span {
  font-weight: bold;
}

@media(max-width: 600px) {
  .planet {
    width: 80%;
  }
}